html, body {
	display: flex;
	flex: 1;
	padding: 0;
	margin: 0;
	background-image: url(../assets/bg.jpg);
	background-size: cover;
	font-family: 'Montserrat', arial, sans-serif;
	font-weight: 400;
	color: #444;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

* {
	box-sizing: border-box;
}

#app {
	display: flex;
	flex: 1;
}



