.hero {
    width: 100%;
    height: 100%;
    color: white;
}

@keyframes slidein {
    0% {
        transform: translate3d(0, -100%, 0);
        opacity: 0;
    }

    50% {
        transform: translate3d(0, -100%, 0);
        opacity: 0;
    }
  
    100% {
        transform: translate3d(0, 0, 0);
        opacity: 1;
    }
}

.first, .second {
    text-align: center;
    will-change: transform;
    animation-name: slidein;
}

.first {
    font-size: 10em;
    animation-duration: 3s;
}

.second {
    font-size: 8em;
    animation-duration: 5s;
}


